.error__wrapper {
  width: 100%;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}
.error__item {
  background-color: #fff;
  padding: 25px;
  border-radius: 10px;
  box-shadow: 0px 0px 30px #fff;
}
.error__text {
  text-align: center;
  font-size: 20px;
  font-weight: 500;
}
